<template>
  <div
    class="chess-board-wrapper"
    :style="{
      width: cellSize * 8,
      height: cellSize * 8,
    }"
  >
    <ChessBoard :cellSize="cellSize" />
    <GamePlayer :moves="movesState" :cellSize="cellSize" />
  </div>
  <div class="controls">
    <input max="85" min="55" class="board-size" type="number" v-model="cellSizeText">
    <textarea
      class="notation-textarea"
      v-model="algNotation"
      rows="25"
      cols="25"
    />
    <button class="parse-button" @click="parseNotation">
      Execute
    </button>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, ref } from 'vue'
import ChessBoard from './ChessBoard.vue'
import GamePlayer from './GamePlayer.vue'
import { tokenizator, normalizeNotation } from './chessNotationParser'
import { Move } from './chessRules'

export default defineComponent({
  components: {
    ChessBoard,
    GamePlayer,
  },
  setup() {
    let cellSize = ref(75)
    let algNotation = ref(
      '1.e2-e4 e7-e5\n' +
      '2.g1-f3 b8-c6\n' +
      '3.d2-d4 e5xd4\n' +
      '4.f1-c4 d7-d6\n' +
      '5.f3xd4 f8-e7\n' +
      '6.0-0 g8-f6\n' +
      '7.b1-c3 0-0\n' +
      '8.h2-h3 c6xd4\n' +
      '9.d1xd4 f6-d7\n' +
      '10.c3-d5 e7-f6\n' +
      '11.d5xf6+ d8xf6\n' +
      '12.c1-e3 f8-e8\n' +
      '13.f2-f3 f6-g6\n' +
      '14.g1-h1 d7-f6\n' +
      '15.g2-g4 h7-h5\n' +
      '16.c4-d3 h5xg4\n' +
      '17.h3xg4 c8xg4\n' +
      '18.f3xg4 f6xg4\n' +
      '19.e3-f4 e8-e6\n' +
      '20.f1-f3 a8-e8\n' +
      '21.f3-h3 c7-c5\n' +
      '22.d4-g1 c5-c4\n' +
      '23.h3-h4 c4xd3\n' +
      '24.c2xd3 g4-h6\n' +
      '25.f4xh6 g6xh6\n' +
      '26.g1-h2 h6xh4\n' +
      '27.h2xh4 e6-h6\n' +
      '28.h4xh6 g7xh6\n' +
      '29.a1-c1 e8-e5\n' +
      '30.c1-c7 e5-b5\n' +
      '31.b2-b3 b5-a5\n' +
      '32.c7xb7 a5xa2\n' +
      '33.b7-d7 a2-a6\n' +
      '34.h1-g2 g8-g7\n' +
      '35.g2-f3 g7-f6\n' +
      '36.f3-e3 f6-e6\n' +
      '37.d7-c7 f7-f5\n' +
      '38.c7-h7 f5xe4\n' +
      '39.e3xe4 a6-b6\n' +
      '40.h7xh6+ e6-d7\n' +
      '41.e4-d5 b6-b5+\n' +
      '42.d5-c4 b5-c5+\n' +
      '43.c4-d4 c5-b5\n' +
      '44.h6-h7+ d7-c6\n' +
      '45.h7xa7 b5xb3\n' +
      '46.d4-c4 b3-b1\n' +
      '47.a7-a6+ c6-d7\n' +
      '48.d3-d4 d7-e6\n' +
      '49.a6-a3'
    )
    let cellSizeText = computed({
      set(value: any) {
        cellSize.value = parseInt(value)
      },
      get: () => cellSize.value
    })
    const movesState = ref<Array<Move>>([])

    const parseNotation = () => {
      movesState.value = tokenizator(
        normalizeNotation(algNotation.value)
      )
    }

    return {
      cellSizeText,
      cellSize,
      algNotation,
      parseNotation,
      movesState,
    }
  }
})
</script>

