  height: 40px;
  width: 200px;
  margin: 0 auto;
  margin: 24px 0 0 0;
}

.board-size {
  width: 200px;
  margin: 0 0 24px 0;
}

.controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.chess-board-wrapper {
  position: relative;
}
</style>
